"use client";

import { useState, useEffect } from 'react';
import type { FormEvent } from 'react';
import { User, Building, Phone, Save } from 'lucide-react';
import { supabase } from '../../../lib/supabase';
import { useAuth } from '../../../contexts/AuthContext';
import {Database} from '@/lib/database.types';
export default function SettingsPage() {
  const { user } = useAuth();
  const [profile, setProfile] = useState<Database["public"]["Tables"]["profiles"]["Row"] | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [message, setMessage] = useState('');
  const [formData, setFormData] = useState({
    full_name: '',
    company_name: '',
    phone: '',
  });
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    if (!user) return;

    const fetchProfile = async () => {
      const { data, error } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', user.id)
        .maybeSingle();

      if (data && !error) {
        setProfile(data);
        setFormData({
          full_name: data.full_name || '',
          company_name: data.company_name || '',
          phone: data.phone || '',
        });
      } else {
        // If profile doesn't exist, create default one
        const { data: newProfile } = await supabase
          .from('profiles')
          .insert([{ id: user.id, email: user.email }])
          .select("*")
          .maybeSingle();
        if (newProfile) {
          setProfile(newProfile);
        }
      }
      setLoading(false);
    };

    fetchProfile();
  }, [user]);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!user) return;

    setSaving(true);
    setMessage('');

    try {
      const updateData: any = {};
      
      if (formData.full_name) updateData.full_name = formData.full_name;
      if (formData.company_name) updateData.company_name = formData.company_name;
      if (formData.phone) updateData.phone = formData.phone;

      const { error } = await supabase
        .from('profiles')
        .update(updateData)
        .eq('id', user.id);

      if (error) {
        console.error("Update error:", error);
        setMessage(`Failed to update profile: ${error.message}`);
      } else {
        // Refresh profile data
        const { data } = await supabase
          .from('profiles')
          .select('*')
          .eq('id', user.id)
          .maybeSingle();
        
        if (data) {
          setProfile(data);
          // keep form data in sync with saved profile
          setFormData({
            full_name: data.full_name || '',
            company_name: data.company_name || '',
            phone: data.phone || '',
          });
        }
        setMessage('Profile updated successfully');
        // exit edit mode after successful save
        setIsEditing(false);
        setTimeout(() => setMessage(''), 3000);
      }
    } catch (err: any) {
      console.error("Error:", err);
      setMessage('An error occurred');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div>
        <div className="p-8 flex items-center justify-center h-screen">
          <div className="text-slate-400">Loading...</div>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="p-8 max-w-4xl">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white mb-2">Settings</h1>
          <p className="text-slate-400">Manage your account preferences</p>
        </div>

        <div className="bg-slate-800 border border-slate-700 rounded-xl p-6 mb-6">
          <h2 className="text-xl font-bold text-white mb-6">Profile Information</h2>

          {message && (
            <div
              className={`mb-6 p-3 rounded-lg ${
                message.includes('success')
                  ? 'bg-green-500/10 border border-green-500/50 text-green-400'
                  : 'bg-red-500/10 border border-red-500/50 text-red-400'
              }`}
            >
              {message}
            </div>
          )}

          {/* Read-only view when not editing */}
          {!isEditing ? (
            <div className="space-y-4">
              <div className="flex items-start justify-between">
                <div>
                  <p className="text-slate-400 text-sm">Email</p>
                  <p className="text-white font-medium">{user?.email || 'N/A'}</p>
                </div>
                <div>
                  <button
                    onClick={() => setIsEditing(true)}
                    className="px-3 py-1 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition-colors"
                  >
                    Edit
                  </button>
                </div>
              </div>

              <div>
                <p className="text-slate-400 text-sm">Full Name</p>
                <p className="text-white">{profile?.full_name || '—'}</p>
              </div>

              <div>
                <p className="text-slate-400 text-sm">Company Name</p>
                <p className="text-white">{profile?.company_name || '—'}</p>
              </div>

              <div>
                <p className="text-slate-400 text-sm">Phone</p>
                <p className="text-white">{profile?.phone || '—'}</p>
              </div>
            </div>
          ) : (
            /* Editable form when in edit mode */
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">Email</label>
                <input
                  type="email"
                  value={user?.email || ''}
                  disabled
                  className="w-full px-4 py-3 bg-slate-900/50 border border-slate-700 rounded-lg text-slate-500 cursor-not-allowed"
                />
                <p className="text-slate-500 text-xs mt-1">Email cannot be changed</p>
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">
                  <div className="flex items-center gap-2">
                    <User className="w-4 h-4" />
                    Full Name
                  </div>
                </label>
                <input
                  type="text"
                  value={formData.full_name}
                  onChange={(e) => setFormData({ ...formData, full_name: e.target.value })}
                  className="w-full px-4 py-3 bg-slate-900 border border-slate-700 rounded-lg text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="John Doe"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">
                  <div className="flex items-center gap-2">
                    <Building className="w-4 h-4" />
                    Company Name
                  </div>
                </label>
                <input
                  type="text"
                  value={formData.company_name}
                  onChange={(e) => setFormData({ ...formData, company_name: e.target.value })}
                  className="w-full px-4 py-3 bg-slate-900 border border-slate-700 rounded-lg text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="Acme Corp"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">
                  <div className="flex items-center gap-2">
                    <Phone className="w-4 h-4" />
                    Phone Number
                  </div>
                </label>
                <input
                  type="tel"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  className="w-full px-4 py-3 bg-slate-900 border border-slate-700 rounded-lg text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="+1 234 567 8900"
                />
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={() => {
                    // discard changes and exit edit mode
                    setFormData({
                      full_name: profile?.full_name || '',
                      company_name: profile?.company_name || '',
                      phone: profile?.phone || '',
                    });
                    setIsEditing(false);
                  }}
                  className="flex-1 px-4 py-2 bg-slate-700 text-white rounded-lg hover:bg-slate-600 transition-colors"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  disabled={saving}
                  className="flex-1 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50 transition-colors"
                >
                  <Save className="w-5 h-5 inline-block mr-2" />
                  {saving ? 'Saving...' : 'Save Changes'}
                </button>
              </div>
            </form>
          )}
        </div>

        <div className="bg-slate-800 border border-slate-700 rounded-xl p-6">
          <h2 className="text-xl font-bold text-white mb-4">Account Details</h2>
          <div className="space-y-3">
            <div>
              <p className="text-slate-500 text-sm">Account Created</p>
              <p className="text-white">
                {profile?.created_at
                  ? new Date(profile.created_at).toLocaleDateString()
                  : 'N/A'}
              </p>  
            </div>
            <div>
              <p className="text-slate-500 text-sm">User ID</p>
              <p className="text-white font-mono text-sm">{user?.id}</p>
            </div>
            <div>
              <p className="text-slate-500 text-sm">Company ID</p>
              <p className="text-white font-mono text-sm">{profile?.company_id || 'N/A'}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
